body {
  margin: 0;
  font-family: 'Poppins', sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.container{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding: 0.5rem 3rem;
}

.item{
  width:200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.79);
  margin: 1rem 1rem;
  padding-bottom: 0.3rem;
  text-align: center;
}
.item:hover{
  transform: scale(1.05);
}
.btn{
  background:rgb(0,0,7);
  padding:0.5rem 1rem ;
  color:white;
  border-radius: 5px;
  border: 1px solid black;
  text-decoration: none;
}
.btn:hover{
  cursor: pointer;
  opacity: 0.7;
}
p{
  margin:0px;
  padding:0.2rem;
}
h4{
  margin:0;
  padding: 0.2rem;
}
.btn:active{
  transform: scale(0.95);
}
img{
  filter: brightness(60%);
}
img:hover{
  filter: brightness(100%);
}   
.material-symbols-outlined {
  color:rgb(178, 2, 2);
  font-weight: bold;
  background-color: rgb(0,0,7);
  padding:0.4rem;  
  border-radius: 5px;
}
.material-symbols-outlined:hover{
  cursor:pointer;
}
.material-symbols-outlined:active{
  transform: scale(0.95);
}
.more-less{
  border: none;
  font-weight: bold;
  text-decoration: underline;
}
.more-less:hover{
  cursor: pointer;
  color:brown
}